const asciiBlue = (/** @type {string} */ str) => `\x1b[34m${str}\x1b[0m`
const asciiGreen = (/** @type {string} */ str) => `\x1b[32m${str}\x1b[0m`
const asciiRed = (/** @type {string} */ str) => `\x1b[31m${str}\x1b[0m`

/**
 * @param {InputType} input
 * @returns
 */
export function findPathWithPredecessors(input) {
	const start = Array2d.traverse(input).find((v) => v.value === "S").pos
	const end = Array2d.traverse(input).find((v) => v.value === "E").pos

	const result = findOptimalRoute(input, start).find((v) => V.eq(v.value.pos, end))

	/** @type {Set<import("../modules/graph.js").PathItem<{pos: V.Vec2, cost: number, dir: number}>>} */
	const visited = new Set()
	/**
	 * @param {import("../modules/graph.js").PathItem<{pos: V.Vec2, cost: number, dir: number}>} item
	 */
	function backtrack(item) {
		visited.add(item)
		for (const p of item.predecessors) backtrack(p)
	}

	backtrack(result)
	return {
		result,
		visited: Array.from(visited).sort((a, b) => a.distance - b.distance),
		start,
		end,
	}
}
